import React from 'react';
import ReactDOM from 'react-dom';
import { InsertFill } from '@klein-editor/icons';
import { IDropPanelMenu, IDomEditor, DomEditor } from '@klein-editor/editor';
import DropPanelContent from '../components/DropPanelContent';
import { insertImageNode, insertLink } from '@klein-editor/basic-modules';
import { insertVideoNode } from '@klein-editor/video-module';
import { emojiConfig } from './config';

export interface CustomEditor {
  onEvent?: (params) => void;
  insertLink?: (url: string, text?: string) => void;
  insertImage?: (src: string, alt?: string, href?: string) => void;
  insertVideo?: (src: string, poster?: string) => void;
  restoreSelection?: () => void;
  handleSelectemotion?: (isShow: boolean, key: string) => void;
  triggerMenu?: (key: string) => void;
}

export default class DropPanelMenu implements IDropPanelMenu {
  title: string;
  tag: string;
  iconSvg: string;
  alwaysShowTitle: boolean;
  showDropPanel: boolean;

  private $content: HTMLDivElement | null = null;
  private $emojiContent: HTMLDivElement | null = null;

  readonly dropPanelMinWidth = 160;
  readonly dropPanelClassName = 'k-e-drop-panel-transparent';

  constructor() {
    this.title = '插入';
    this.tag = 'button';
    this.iconSvg = InsertFill;
    this.alwaysShowTitle = true;
    this.showDropPanel = true;
  }

  // 菜单是否需要激活（如选中加粗文本，“加粗”菜单会激活），用不到则返回 false
  isActive(editor: IDomEditor): boolean {
    return false;
  }

  // 获取菜单执行时的 value ，用不到则返回空 字符串或 false
  getValue(editor: IDomEditor) {
    return '';
  }

  // 菜单是否需要禁用（如选中 H1 ，“引用”菜单被禁用），用不到则返回 false
  isDisabled(editor: IDomEditor): boolean {
    return false;
  }

  // 点击菜单时触发的函数
  exec(editor: IDomEditor, value: string | boolean) {
    // DropPanel menu ，这个函数不用写，空着即可
    if (this.$emojiContent) this.$emojiContent.style.display = 'none';
  }

  // 定义 DropPanel 内部的 DOM Element
  getPanelContentElem(editor: IDomEditor): Element {
    if (!this.$content) this.$content = document.createElement('div');
    const { uploadMode, menuItemKeys, menuItems } =
      editor.getMenuConfig('insertMenu') || {};
    ReactDOM.render(
      <DropPanelContent
        uploadMode={uploadMode}
        menuItems={menuItems}
        menuItemKeys={menuItemKeys}
        editor={{
          onEvent: (params) => editor.emit('event', params),
          insertLink: (url, text) => this.insertLink(editor, url, text),
          insertImage: (src, alt, href) =>
            this.insertImage(editor, src, alt, href),
          insertVideo: (src, poster) => this.insertVideo(editor, src, poster),
          restoreSelection: () => this.restoreSelection(editor),
          handleSelectemotion: (isShow, key) =>
            this.handleSelectemotion(editor, isShow, key),
          triggerMenu: (key: string) => this.triggerMenu(editor, key),
        }}
        getPopupContainer={() => this.$content as HTMLElement}
        getEmotionContainer={() => this.$emojiContent as HTMLElement}
      />,
      this.$content,
    );
    return this.$content;
  }

  private insertLink(editor: IDomEditor, url: string, text: string = '') {
    // 插入链接
    insertLink(editor, text || url, url);
  }

  private insertImage(
    editor: IDomEditor,
    src: string,
    alt: string = '',
    href: string = '',
  ) {
    if (!src) return;

    // 插入图片
    insertImageNode(editor, src, alt, href);
  }

  private insertVideo(editor: IDomEditor, src: string, poster = '') {
    // 插入视频
    insertVideoNode(editor, src, poster);
  }

  private restoreSelection(editor: IDomEditor) {
    // 还原选区
    editor.restoreSelection();
  }

  private handleSelectemotion = (editor: IDomEditor, isShow: boolean, key) => {
    const $pannelMenuContent = this.$content?.querySelector(
      '.klein-dropdown-placement-bottomCenter',
    );
    if (this.$emojiContent == null && key === 'emoji') {
      // 第一次渲染
      const $emojiContent = document.createElement('ul') as any;
      $emojiContent.classList.add('k-e-panel-content-emotion');
      $emojiContent.style.left = `${
        $pannelMenuContent?.getBoundingClientRect().width
      }px`;
      $emojiContent.onclick = function (e) {
        const { target } = e;
        if (target == null) return;
        e.preventDefault();
        if (target.tagName.toLowerCase() === 'li') {
          const li = this.querySelectorAll('li');
          Array.prototype.indexOf.call(li, target);
          editor.insertText(target.innerText || '');
        }
      };
      // 根据菜单配置生成 panel content
      emojiConfig().forEach((emotion: string) => {
        const $li = document.createElement('li');
        $li.innerText = `${emotion}`;
        $emojiContent.append($li);
      });
      this.$emojiContent = $emojiContent;
      $pannelMenuContent?.append($emojiContent);
      return;
    }
    if (isShow && this.$emojiContent)
      this.$emojiContent.style.display = 'block';
    if (!isShow && this.$emojiContent)
      this.$emojiContent.style.display = 'none';
  };
  // 触发menu
  private triggerMenu(editor: IDomEditor, key: string) {
    const menuItemFactories = editor.getMenuItemFactories();
    const factory = menuItemFactories[key];
    const menu = factory?.();
    console.log(menu, 'menumenu');
    if (menu) {
      const disabled = menu.isDisabled(editor);
      if (!disabled) {
        const val = menu.getValue(editor);
        menu.exec(editor, val); // 执行 menu 命令
      }
    }
  }
}
