import React, { forwardRef, useImperativeHandle, useRef } from 'react';
import type {
  IDomEditor,
  IEditorConfig,
  IToolbarConfig,
} from '@klein-editor/core';

import Editor from './components/editor';
import Toolbar from './components/toolbar';
import { KleinEditorProps } from './interface';

import '@klein-editor/editor/dist/css/style.css';
import '@wemo-ui/klein/lib/menu/style';
import '@wemo-ui/klein/lib/dropdown/style';

const KleinEditor: React.FC<KleinEditorProps> = React.memo(
  forwardRef((props, ref) => {
    const [editor, setEditor] = React.useState<IDomEditor | null>(null);

    const editorRef = useRef<IDomEditor | null>(null);

    useImperativeHandle(ref, () => ({
      getEditor: () => {
        return editorRef.current;
      },
    }));

    const {
      className,
      style,
      toolbarClassName,
      toolbarStyle,
      wrapClassName,
      containerId = 'klein-editor',
      wrapStyle,
      toolbarConfig: tConfig,
      mode = 'default',
      h5Mode = false,
      onChange,
      ...eConfig
    } = props;

    const toolbarConfig = useRef<Partial<IToolbarConfig>>({
      h5Mode,
      containerId,
      ...(tConfig || {}),
    });

    const editorConfig = useRef<Partial<IEditorConfig>>({
      placeholder: '请输入内容...',
      h5Mode,
      containerId,
      ...(eConfig || {}),
    });

    const handleCreated = (editor: IDomEditor) => {
      editorRef.current = editor;
      setEditor(editor);
    };

    const handleChange = (editor: IDomEditor) => {
      const isEmpty = editor.isEmpty();
      if (isEmpty) {
        onChange?.('');
        return;
      }
      onChange?.(editor.getHtml(), editor.getText());
    };

    React.useEffect(() => {
      return () => {
        if (editor == null) return;
        editor.destroy();
        setEditor(null);
      };
    }, [editor]);

    return (
      <div
        style={wrapStyle}
        className={`klein-editor ${wrapClassName || ''}`}
        id={containerId}
      >
        <Toolbar
          className={toolbarClassName}
          style={toolbarStyle}
          mode={mode}
          editor={editor}
          defaultConfig={toolbarConfig.current}
        />
        <Editor
          className={className}
          style={style}
          defaultConfig={editorConfig.current}
          mode={mode}
          onChange={handleChange}
          onCreated={handleCreated}
        />
      </div>
    );
  }),
);
export default KleinEditor;
