import React, { useRef, useEffect, useState } from 'react';
import {
  SlateDescendant,
  IEditorConfig,
  createEditor,
  IDomEditor,
  SlateEditor,
  SlateTransforms,
} from '@klein-editor/editor';

interface IProps {
  defaultContent?: SlateDescendant[];
  onCreated?: (editor: IDomEditor, textareaDom: HTMLDivElement | null) => void;
  defaultHtml?: string;
  onChange: (editor: IDomEditor) => void;
  defaultConfig: Partial<IEditorConfig>;
  mode?: string;
  style?: React.CSSProperties;
  className?: string;
  onEvent?: (editor: IDomEditor, params: any) => void;
}

function EditorComponent(props: Partial<IProps>) {
  const {
    defaultContent = [],
    onCreated,
    defaultHtml = '',
    onChange,
    defaultConfig = {},
    mode = 'default',
    style = {},
    className,
    onEvent,
  } = props;
  const ref = useRef<HTMLDivElement>(null);
  const [editor, setEditor] = useState<IDomEditor | null>(null);

  const handleCreated = (editor: IDomEditor) => {
    // 组件属性 onCreated
    if (onCreated) onCreated(editor, ref.current);

    // 编辑器 配置 onCreated
    const { onCreated: onCreatedFromConfig } = defaultConfig;
    if (onCreatedFromConfig) onCreatedFromConfig(editor, ref.current);
  };

  const handleChanged = (editor: IDomEditor) => {
    // 组件属性 onChange
    if (onChange) onChange(editor);

    // 编辑器 配置 onChange
    const { onChange: onChangeFromConfig } = defaultConfig;
    if (onChangeFromConfig) onChangeFromConfig(editor);
  };

  const handleDestroyed = (editor: IDomEditor) => {
    const { onDestroyed } = defaultConfig;
    setEditor(null);
    if (onDestroyed) {
      onDestroyed(editor);
    }
  };

  // 各种自定义事件：如插入商品等
  const handleEvent = (editor: IDomEditor, params: any) => {
    // 组件属性 onEvent
    onEvent?.(editor, params);

    // 编辑器 配置 onChange
    const { onEvent: onEventFromConfig } = defaultConfig;
    onEventFromConfig?.(editor, params);
  };
  // value 变化，
  useEffect(() => {
    if (editor == null) return;
    if (ref.current === null) return;
  }, [ref.current, editor]);

  useEffect(() => {
    if (ref.current == null) return;
    if (editor != null) return;
    // 防止重复渲染 当编辑器已经创建就不在创建了
    if (ref.current?.getAttribute('data-k-e-textarea')) return;

    const newEditor = createEditor({
      selector: ref.current,
      config: {
        ...defaultConfig,
        onCreated: handleCreated,
        onChange: handleChanged,
        onDestroyed: handleDestroyed,
        onEvent: handleEvent,
      },
      content: defaultContent,
      html: defaultHtml || '',
      mode,
    });
    setEditor(newEditor);
  }, [editor]);

  return <div style={style} ref={ref} className={className}></div>;
}

export default EditorComponent;
