import React from 'react';
import { Dropdown, Menu } from '@wemo-ui/klein';
import { CustomEditor } from '../menu/DropPanelMenu';
import { MenuItemProps } from '../custom-types';

const { SubMenu } = Menu;

interface IProps {
  menuItemKeys: string[];
  menuItems: MenuItemProps[];
  editor: CustomEditor;
  uploadMode?: 'upload' | 'material';
  getPopupContainer: () => HTMLElement;
  getEmotionContainer: () => HTMLElement;
}

interface ImageSvgProps {
  svg: string;
}

const ImageSVG: React.FC<ImageSvgProps> = (props) => {
  const { svg } = props;
  return (
    <img
      style={{ verticalAlign: 'text-bottom', marginRight: 8 }}
      src={`data:image/svg+xml;utf8,${svg}`}
    />
  );
};

const DropPanelContent: React.FC<IProps> = (props) => {
  const {
    menuItems,
    menuItemKeys,
    editor,
    uploadMode,
    getEmotionContainer,
    getPopupContainer,
  } = props;

  const handleSelectMaterial = (materialType: number) => {
    const win = window as any;
    const invoke = win?.kraken?.invoke;
    if (typeof invoke === 'function' && uploadMode === 'material') {
      invoke('showModal')('MPMaterialSelect', {
        name: 'MPMaterialSelect',
        modalParams: {
          visible: true,
          destroyOnClose: true,
          width: 900,
          bodyStyle: {
            padding: 0,
          },
          footer: null,
          closable: false,
        },
        compParams: {
          materialType,
          uploadItemProps: {
            // TODO: 待确认传入方式
            env: (window as any).saasData.mpmaterial.wmc.env,
          },
        },
      }).then(function (data) {
        const { title, url, newsDetailResponseDTOList, materialDetailExts } =
          data[0] || {};
        switch (materialType) {
          case 0:
            const { accessUrl, title: tuwenTitle } =
              newsDetailResponseDTOList?.[0] || {};
            editor?.insertLink?.(accessUrl, tuwenTitle);
            break;
          case 1:
            editor?.insertImage?.(url, title);
            break;
          case 3:
            const [posterJson] = materialDetailExts || [{}];
            editor?.insertVideo?.(url, posterJson?.value || title);
            break;
        }
      });
    } else {
      switch (materialType) {
        case 1:
          editor?.triggerMenu?.('uploadImage');
          break;
        case 3:
          editor?.triggerMenu?.('uploadVideo');
          break;
        default:
          break;
      }
    }
  };

  const handleSelect = (e: any) => {
    e.domEvent.preventDefault();
    const key = e.key;
    if (key !== 'emoji') editor?.handleSelectemotion?.(false, key);
    switch (key) {
      case 'tuwen':
        handleSelectMaterial(0);
        break;
      case 'image':
        handleSelectMaterial(1);
        break;
      case 'video':
        handleSelectMaterial(3);
        break;
      case 'emoji':
        const currentStyle =
          getComputedStyle(
            getEmotionContainer() || getPopupContainer(),
            null,
          ) || {};
        editor?.handleSelectemotion?.(
          currentStyle?.display === 'none' ? true : false,
          key,
        );
        break;
      default:
        editor?.restoreSelection?.();
        break;
    }
    editor?.onEvent?.({
      menuKey: 'insertMenu',
      key,
    });
  };

  const menu =
    menuItemKeys?.length && menuItems?.length ? (
      <Menu className="drop-panel-menu" mode="vertical" onClick={handleSelect}>
        {menuItems?.map(({ key, title, icon, children }) => {
          if (!menuItemKeys.includes(key)) return null;
          if (children?.length) {
            return (
              <SubMenu
                key={key}
                title={title}
                icon={icon ? <ImageSVG svg={icon} /> : null}
              >
                {children.map(({ key: cKey, title: cTitle }) => {
                  if (!menuItemKeys.includes(cKey)) return null;
                  return <Menu.Item key={cKey}>{cTitle}</Menu.Item>;
                })}
              </SubMenu>
            );
          }
          return (
            <Menu.Item
              className={key === 'emoji' ? 'drop-panel-menu-item-emoji' : ''}
              key={key}
              icon={icon ? <ImageSVG svg={icon} /> : null}
            >
              {title}
            </Menu.Item>
          );
        })}
      </Menu>
    ) : (
      <></>
    );

  return (
    <Dropdown
      visible={true}
      placement="bottomCenter"
      overlay={menu}
      getPopupContainer={getPopupContainer}
    >
      <></>
    </Dropdown>
  );
};

export default DropPanelContent;
